import React from "react";
import "./App.css";

const schedule = {
  Monday: ["음악", "유도", "영어", "학스", "사회", "국어", "진업"],
  Tuesday: ["기가", "미술", "사회", "예미", "국어", "과학", "주화"],
  Wednesday: ["영어", "수학", "국어", "기가", "도덕", "사회", "진"],
  Thursday: ["영어", "창A", "국어", "수학", "과학", "창1", "주목"],
  Friday: ["체육", "기가", "수학", "예체", "도덕", "자동", "?"],
  Sunday: ["!"],
  Saturday: ["!"],
};

// const time = [
//   "AM 09:20 - AM 10:05",
//   "AM 10:05 - AM 10:50",
//   "AM 10:50 - AM 11:35",
//   "?",
//   "AM 12:40 - PM 01:25",
//   "PM 01:25 - PM 01:",
//   "PM 03:20 - PM 04:00",
// ];

var d = new Date();
var weekday = new Array(7);
weekday[0] = "Sunday";
weekday[1] = "Monday";
weekday[2] = "Tuesday";
weekday[3] = "Wednesday";
weekday[4] = "Thursday";
weekday[5] = "Friday";
weekday[6] = "Saturday";

var n = weekday[d.getDay()];

function App() {
  return (
    <div className="container">
      <div className="schedules">
        <div className="day">{n.slice(0, 3)}</div>
        {schedule[n].map((props, index) => {
          return (
            // <div className="one">
            // <div className="time">{time[index]}</div>
            <span className="schedule" key={index}>
              {props}
            </span>
            // </div>
          );
        })}
      </div>

      <div className="info">
        <div className="reason">내가 쓰고 싶어서 만든</div>
        <div className="WIT">우리 반 시간표 웹 앱</div>
        <div className="WMT">제작자 : 후스</div>
        <div className="repo">
          레포지토리 :{" "}
          <a href="https://github.com/r-hooss/today-class">click here</a>
        </div>
      </div>
    </div>
  );
}

export default App;
